import { AvmValueType } from "avmore/avm-value";
import { NativeHost } from "avmore/host";
import { Vm } from "avmore/vm";
import { MovieClipBindings } from "./native/movie-clip";
import { NativeSlot, setNativeSlot } from "./slots";
const REALM = new Map();
const AVM_SPRITE_CACHE = new WeakMap();
function getAvmSprite(vm, sprite) {
    let external = AVM_SPRITE_CACHE.get(sprite);
    if (external === undefined) {
        const target = vm.newObject();
        target.prototype = MovieClipBindings.getOrCreate(vm, REALM);
        // const ownProperties: Map<string, AvmValue> = new Map();
        external = vm.newExternal({
            ownKeys() {
                return [...target.ownProperties.keys()]
                    .map(value => ({ type: AvmValueType.String, value }));
            },
            get(key) {
                let value = vm.tryGetMember(target, key);
                if (value === undefined) {
                    const namedChild = sprite.namedChildren.get(key);
                    if (namedChild !== undefined) {
                        value = getAvmSprite(vm, namedChild);
                    }
                }
                return value;
            },
            set(key, value) {
                vm.setMember(target, key, value);
            },
        });
        setNativeSlot(external, NativeSlot.SPRITE, sprite);
        AVM_SPRITE_CACHE.set(sprite, external);
    }
    return external;
}
const TARGET_BY_ID = new Map();
const TARGET_TO_ID = new Map();
export function createAvm1Context() {
    const vm = new Vm();
    const nativeHost = new NativeHost();
    const host = {
        trace: nativeHost.trace,
        getTarget(targetId) {
            const target = TARGET_BY_ID.get(targetId);
            if (target === undefined) {
                // TODO: Warn (unknown target)
                return undefined;
            }
            return {
                stop() {
                    target.stop();
                },
            };
        },
    };
    return { executeActions };
    function executeActions(target, avm1Bytes) {
        let targetId = TARGET_TO_ID.get(target);
        if (targetId === undefined) {
            targetId = TARGET_TO_ID.size;
            TARGET_TO_ID.set(target, targetId);
            TARGET_BY_ID.set(targetId, target);
        }
        const external = getAvmSprite(vm, target);
        const scriptId = vm.createAvm1Script(avm1Bytes, targetId, external);
        vm.runToCompletion(scriptId, host);
    }
}
