/**
 * The first tick is after `1000 / frameRate` ms.
 *
 * @param clock Clock used to control the loop.
 * @param frameRate Ticks per second
 * @param onTick
 * @return {Loop}
 */
function startLoop(clock, frameRate, onTick) {
    const startTime = clock.getTime();
    let oldTime = startTime;
    let shift = 0;
    let nextTickCount = 0;
    let handle = undefined;
    function handleTick() {
        onTick();
        scheduleNextTick();
    }
    function scheduleNextTick() {
        const curTime = clock.getTime();
        if (nextTickCount > 0 && curTime === oldTime) {
            // tslint:disable-next-line:max-line-length
            const infoUri = "https://developer.mozilla.org/en-US/docs/Web/API/DOMHighResTimeStamp#Reduced_time_precision";
            console.warn(`\`curTime === oldTime\`, possible reduced time precision, see ${infoUri}`);
        }
        oldTime = curTime;
        // console.log(clock.getTime());
        nextTickCount++;
        const targetTime = startTime + shift + (1000 * nextTickCount / frameRate);
        let timeout = targetTime - clock.getTime();
        if (timeout < 0) {
            shift += -timeout;
            console.warn(`Unable to maintain frameRate (missed by ${-timeout}ms)`);
            timeout = 0;
        }
        handle = clock.setTimeout(timeout, handleTick);
    }
    scheduleNextTick();
    function destroy() {
        if (handle !== undefined) {
            clock.clearTimeout(handle);
            handle = undefined;
        }
    }
    return { destroy };
}
