import { createDomuPlayer } from "./domu-player";
// Default size of an <embed> element (Firefox 59)
const DEFAULT_WIDTH = 240;
const DEFAULT_HEIGHT = 200;
// https://helpx.adobe.com/flash/kb/flash-object-embed-tag-attributes.html
export class XSwfElement extends HTMLElement {
    get name() {
        return this.getAttribute("name") || "";
    }
    set name(value) {
        this.setAttribute("name", value);
    }
    get src() {
        return this.getAttribute("src") || "";
    }
    set src(value) {
        this.setAttribute("src", value);
    }
    get width() {
        return this.getAttribute("width") || "";
    }
    set width(value) {
        this.setAttribute("width", value);
    }
    get height() {
        return this.getAttribute("height") || "";
    }
    set height(value) {
        this.setAttribute("height", value);
    }
    constructor() {
        super();
        const size = this.getSize();
        this.domuPlayer = createDomuPlayer({ container: this, viewportSize: size });
        this.style.display = "inline-block";
        this.style.width = `${size.width}px`;
        this.style.height = `${size.height}px`;
        const src = this.getAttribute("src");
        if (src === null) {
            return;
        }
        this.domuPlayer.setMovieUrl(src);
    }
    getSize() {
        const widthAttr = this.getAttribute("width");
        let width = widthAttr === null ? DEFAULT_WIDTH : parseInt(widthAttr, 10);
        if (isNaN(width)) {
            width = DEFAULT_WIDTH;
        }
        const heightAttr = this.getAttribute("height");
        let height = heightAttr === null ? DEFAULT_HEIGHT : parseInt(heightAttr, 10);
        if (isNaN(width)) {
            height = DEFAULT_HEIGHT;
        }
        return { width, height };
    }
    static get observedAttributes() {
        return [
            "width",
            "height",
            "src",
            // "pluginspage",
            "src",
            "id",
            "play",
            "loop",
            "menu",
            "quality",
            "scale",
            "align",
            "salign",
            "wmode",
            "bgcolor",
            "base",
            "allowFullScreen",
            "fullScreenAspectRatio",
            "flashvars",
            "browserzoom",
        ];
    }
}
